import { Component } from '@angular/core';
import {IonicPage, NavController, NavParams, Platform} from 'ionic-angular';
import {DrupalPost} from "../../providers/info/models/drupal-post.model";
import {DomSanitizer, SafeHtml} from "@angular/platform-browser";

/**
 * Generated class for the InfoItemPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-info-item',
  templateUrl: 'info-item.html',
})
export class InfoItemPage {

  post: DrupalPost;
  safeHtml: SafeHtml;

  constructor(public platform: Platform,public navCtrl: NavController,navParams: NavParams, sanitizer: DomSanitizer) {
    this.post = <DrupalPost>navParams.get('item');
    this.safeHtml = sanitizer.bypassSecurityTrustHtml(this.post.content);
  }

  openUrl() {

    this.platform.ready().then(() => {
    //  let browser = new InAppBrowser("https://www.techiediaries.com",'_blank');
    });
  }
  ionViewDidLoad() {
  }

}



